.news-card-main{
    width: 100%;
    height: 100%;
    padding: 3%;
    background: white;
    border-radius: 5px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.news-title{
    text-align: start;
    width: 100%;
    height: 20%;
    color: #2196F3;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.2vw;
    margin-bottom: 3%;
    box-sizing: border-box;
    font-weight: bold;
    cursor: pointer;
}
.news-body{
    width: 100%;
    height: 60%;
}
.news-image{
    width: 30%;
    height: 100%;
    float: left;
}
.news-content{
    margin-left: 5%;
    width: 65%;
    height: 100%;
    text-align: start;
    float: left;
    font-size: 0.93vw;
    color: #607D8B;
    font-family: 'Montserrat', sans-serif;
}
.news-events{
    width: 100%;
    height: 10%;
    padding-top: 1%;
}
.news-author{
    width: 50%;
    height: 100%;
    font-size: 0.9vw;
    float: left;
    text-align: start;
    color : #FF5722 ;
}
.news-date{
    width: 50%;
    height: 100%;
    float: left;
    text-align: end;
    font-size: 0.9vw;
    color : #FF5722 ;

}
.news-body-main{
    padding: 0.7%;
}
.news-cards-div{
    width: 43%;
    height: 29%;
    margin: 2.2%;
    float: left;
}
.filter-box-div{
    margin-left: 2%;
    width: 100%;
    height: 12%;
    margin-bottom: 0.2%;
}
.news-body-div{
    width: 100%;
    height: 90%;
    max-height: 90%;
    overflow-y: scroll;
}
.filter-box-main{
    width: 60%;
    height: 100%;
    background: white;
    margin-right: 1%;
    border-radius: 5px;
    float: left;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.temperature-box-main{
    width: 32%;
    height: 100%;
    background: white;
    margin-right: 1%;
    border-radius: 5px;
    float: left;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
}
.search-box-div{
    margin-left: 2%;
    width: 95%;
    height: 100%;
    float: left;
}
.dropdown-box-div{
    width: 20%;
    height: 100%;
    float: left;
}
.temp-events{
    width: 20%;
    text-align: center;
    float: left;
    font-family: 'Montserrat', sans-serif;
    vertical-align: middle;
    padding-top: 4%;
}
.event-value{
    font-size: 1.5vw;
    margin-bottom: 2%;
}
.event-type{
    font-size: 0.8vw;
}
.meaurement-unit{
    font-size: 1vw;
}
.App {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow-y: scroll;
  background: #CFD8DC;  /* fallback for old browsers */
  text-align: center;
  pointer-events: auto;
}
Router{
  width: 100%;
  height: 100%;
}
.navbar-section{
  width: 100%;
  height: 10%;
}
.pages{
  width: 100%;
  height: 90%;
}
.main{
  width: 100%;
  height: 100%;
}
.App-link {
  color: #61dafb;
}
#overlay {
  padding-top:20%;
  display: inline-block;
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.3);
  z-index: 2;
  cursor: pointer;
}
